function headerUiController() {
  var el,
    _menu,
    _depth1,
    _depth1Item,
    _depth2,
    _depth2Item

  function init() {
    el = $('.left-area');
    _menu = el.find('.menu');
    _depth1 = _menu.find('.depth1');
    _depth1Item = _depth1.find('.menu_d1');

    _depth2 = _depth1.find('.depth2');
    _depth2Item = _depth2.find('a');
    setGnb();
    return this;
  }


  function setGnb() {
    _depth1Item.off().on('click', function (e) {
      e.stopPropagation();

      if ($(this).parent().hasClass('active')) {
        $(this).next('.depth2').stop().slideUp(200);
        $(this).parent().removeClass('active');
        return false;
      } else {
        $(this).parent().addClass('active');
      }

    });
  }
  return init();
}


function leftAccordionMenu(){
  $(".js-accordion_menu").trpAccordionMenu(".js-acc_btn", ".depth2", "active", function($index){
    if ( $index < 0) { return; } 					// 0 보다 작으면 아무것도 하지 않는다.
    if ( $(".js-left_area").hasClass("closed") ) { return; } // 최소화 크기 일때 실행 안함

    var _this        = ".js-accordion_menu";
    var _subcon      = ".depth2";
    var _activeClass = "active";
    // 활성화되어있으면 전부 닫침.
    if( $(_this).find(">li[data-index = "+$index+"]").hasClass(_activeClass) ){  
      $(_this).find(">li").removeClass(_activeClass);
      $(_this).find(_subcon).slideUp();
      return false;
    } 

    $(_this).find(">li").removeClass(_activeClass);
    $(_this).find(">li[data-index = "+$index+"]").addClass(_activeClass);

    $(_this).find(_subcon).slideUp();
    $(_this).find(">li[data-index = "+$index+"]").find(_subcon).slideDown();


  });

  $('.js-menu_closed').on('click', function () {
    $('.js-left_area').toggleClass('closed');
    $('.con-wrap').toggleClass('closed');
  })
}


/* TabMenu (상단)  */
function tabs() {
  var _tabs    = $(".js-topTabs .tab"); // 버튼
  var _tabBtn  = _tabs.find("a");
  
  _tabBtn.on('click', function ($e) {
    $e.preventDefault();
    var idx    = $(this).parent().index();
    var _tabCont     =  $(this).parent().data("tabcon")  //  텝컨텐츠 케이스
    var _tabContidx  =  $(this).parent().data("tabidx"); //  텝컨텐츠 아이디
    var _tabContActive =   "."+_tabCont+ "."+_tabContidx;
    //console.log("_tabContActive:  ", _tabContActive)

    // 텝
    _tabs.removeClass("active");
    _tabs.eq(idx).addClass("active");

    // 텝 컨텐츠
    $("."+_tabCont).removeClass("active").hide();
    $(_tabContActive).addClass("active").show();
    
  });
}

/* TabMenu (컨텐츠 안) */
function tab_box() {
  $(".text-box-wrap , .tabmenu-wrap").each(function () {
    var $myTabs = $(this);

    $myTabs.find("ul.tabs li a").on("click", function ($e) {
      $e.preventDefault();
      var $this = $(this);
      $this.parent().addClass("active").siblings().removeClass("active");
      
      var activeTab = $this.attr("href");
      $myTabs.find(".tab-content_tiem").removeClass("active").hide();
      //$(activeTab).addClass("active").fadeIn();
      $(activeTab).addClass("active").show();

      return false;
    });
  });
  $(".text-box-wrap").each(function () {
    var $myTabs = $(this);

    $myTabs.find("ul.tabs li a").on("click", function ($e) {
      $e.preventDefault();
      var $this = $(this);

      $this.parent().addClass("active").siblings().removeClass("active");
      $myTabs.find(".tab-content_tiem").hide();

      var activeTab = $this.attr("href");
      $(activeTab).fadeIn();

      return false;
    });
  });
}


/* === ready === */
$(function(){

  leftAccordionMenu();  // 싸이드 메뉴
  tabs(); 
  tab_box ();

});